.divider-container{
    margin-top: 20px;
    &.divider-type-1{
        h3{
            padding: 54px 10px;
            text-align: center;
            background-color: #d8d8d8;
            margin-bottom: 22px;
        }
        p{
            width: 55%;
            margin: 0 auto 25px;
            text-align: center;
            
        }

    }
    &.divider-type-2{
        h3{
            padding: 54px 10px;
            text-align: center;
            background-color: #d8d8d8;
            margin-bottom: 22px;
        }
        p{
            width: 55%;
            margin: 0 auto 25px;
            text-align: center;
            
        }

    }
    &.divider-type-3{
        .divider-header{
            position: relative;
            margin-bottom: 20px;
            width: 100%;
            display: flex;
            justify-content: center;
            &:before{
                content: '';
                border: 1px solid #979797;
                width: 100%;
                position: absolute;
                top: 50%;
                left: 0;
            }
        }

        h3{
            text-align: center;
            font-size: 30px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.17;
            letter-spacing: 0.5px;
            text-align: center;
            color: #3d3d3d;
            font-family: var(--uniqloProReg);
            position: relative;
            background-color: #fff;
            padding: 0 10px;
           
        }
        p{
            width: 55%;
            margin: 0 auto 25px;
            text-align: center;
            
        }

    }
  
}