$uniqloReg: 'Uniqlo Pro Regular',
'$uniqloRegregular',
'Helvetica Neue',
Helvatica,
Arial;

$uniqloBold: 'Uniqlo Pro Bold',
'$uniqloRegbold',
'Helvetica Neue',
Helvatica,
Arial;

$heleveticaNeue: 'Helvetica Neue',
Helvatica,
Arial;



.Title-1 {
  font-family: $uniqloReg;
  font-size: 50px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.04;
  letter-spacing: 0.5px;
  color: #0d0d0d;
}


.Title-3 {
  font-family: $uniqloReg;
  font-size: 50px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.1;
  letter-spacing: 1.5px;
  color: #0d0d0d;
}


.Title-2 {
  font-family: $heleveticaNeue;
  font-size: 45px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.16;
  letter-spacing: normal;
  color: #0d0d0d;
}

.Header-1,
h1 {
  font-family: $uniqloReg;
  font-size: 30px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: 0.5px;
  color: var(--black);
}

.Header-3,
h2 {
  font-family: $uniqloReg;
  font-size: 22px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.36;
  letter-spacing: 0.5px;
  color: var(--black);
}

.Header-2,
h3 {
  font-family: $heleveticaNeue;
  font-size: 20px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  color: var(--black);
}

.Header-4,
h4 {
  font-family: $uniqloReg;
  font-size: 18px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.39;
  letter-spacing: 0.7px;
  color: #333333;
}

.Body-1-Bold {
  font-family: $heleveticaNeue;
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.44;
  letter-spacing: normal;
  color: var(--black);
}

.Body-1-Medium {
  font-family: $heleveticaNeue;
  font-size: 16px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.44;
  letter-spacing: normal;
  color: var(--black);
}

.Body-1-Regular,
p {
  font-family: $heleveticaNeue;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.44;
  letter-spacing: normal;
  color: var(--black);
}

.Body-1-Italic {
  font-family: $heleveticaNeue;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: italic;
  line-height: 1.44;
  letter-spacing: normal;
  color: var(--black);
}

.Body-1-Caption {
  font-family: $heleveticaNeue;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  color: var(--black);
}