@import '../../default/breakpoints';

.description-container {
    width: 55%;

    &.description-type-1 {
        margin: 20px auto 50px;
        text-align: center;

        h3 {
            text-align: center;
            margin-bottom: 20px;
        }

        p {
            text-align: center;
            margin-bottom: 20px;
        }

        .Body-1-Caption {
            text-align: center;
        }
    }

    &.description-type-2 {
        margin: 20px auto 30px;

        h3 {
            margin-bottom: 15px;
            text-align: center;
        }

        p {
            margin-bottom: 25px;
        }
    }

    &.description-type-3 {
        margin: 35px auto;

        h3 {
            margin-bottom: 25px;
            text-align: center;
        }

        p {
            font-size: 22px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.36;
            letter-spacing: 1px;
            text-align: center;
            color: #000000;
            margin-bottom: 20px;

        }
    }

    &.description-type-4 {
        margin: 10px auto 35px ;
        padding: 10px 40px 40px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 70px;
        width: 100%;
      
        div{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
        }

        h3 {
            margin-bottom: 25px;
            display: block;
            width: 100%;
        }

        p {
            margin-bottom: 20px;
            width: 100%;
        }
    }
}