.products-1,
.products-2-main,
.products-3 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  .product-body {
    width: 50%;
  }
}

.products-2 {
  width: 100%;

  .Header-2 {
    margin: 30px 0;
  }
}

.products-3 {
  .product-body {
    .temp-img-container {
      float: left;
      margin-right: 20px;
    }
  }
}

.products-4 {
  .Header-2 {
    width: 100%;
    margin: 30px auto;
    text-align: center;
  }
  .product-body {
    .Header-2 {
      margin-top: 50px;
    }

    .title-subheading {
        width: 700px;
        max-width: none;
    }
  }
}

.product-feature {
  max-width: 500px;
  margin: 60px auto;

  .prod-feat-title {
    margin-bottom: 10px;
    text-transform: uppercase;
  }
}

.product-carousel {
  display: flex;
  justify-content: space-between;

  .product-in-carousel {
  }
}
