.custom-slide {
    padding-top: 50px;
    width: 200px;
    transform: translateX(-250px);
    transition: .3s;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    height: 100vh;
    overflow: scroll;

}


.custom-slide li {
    margin-bottom: 15px;
    border: 1px solid black;
    padding: 5px 10px;
    margin-right: 20px;
    border-radius: 2px;
    cursor: pointer;
    transition: .08s;
}

.custom-slide li:hover {
    color: white;
    background-color: black;
}

.slide-show {
    transform: translateX(0);
}

.page {
    margin: 0 auto;
    width: 100%;
    &.page-pc {
        max-width: 1280px;
    }
    &.page-tb {
        max-width: 768px;
    }

    &.page-mv {
        max-width: 375px;
    }
}


.App {
    background-color: #f1f1f1;
    min-height: 100vh;
    }
    